@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap');

@import 'variables';
@import 'button';
@import 'input-and-dropdown';
@import 'main-containers';
@import 'outliers-element';
@import 'starship-element';

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html,
.main-container {
  color: $font-color;
  background-color: $main-color;
  font-family: $main-font;
  width: 100%;
  height: 100vh;
  background-image: url('../images/space.jpg');
  background-size: 100%;
}

.amount-selector-text,
.spaceships-selector-text {
  text-align: center;
  margin: 0.5rem;
  font-size: 1.2rem;
}

.input-num-form {
  margin: 0.5rem;
}

.dropdown-form {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.dropdowns-container {
  text-align: center;
}

.output-elements-wrapper {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(400px, 1fr));
  justify-content: center;
}
